$ll-font-path: '../webfonts' !default;
$ll-font-display: swap !default;

/* maitree-200 - latin */
@font-face {
  font-display: swap; /* Check https://developer.mozilla.org/en-US/docs/Web/CSS/@font-face/font-display for other options. */
  font-family: 'Maitree';
  font-style: normal;
  font-weight: 200;
  src: url('#{$ll-font-path}/maitree/maitree-v10-latin-200.eot'); /* IE9 Compat Modes */
  src: url('#{$ll-font-path}/maitree/maitree-v10-latin-200.eot?#iefix')
      format('embedded-opentype'),
    /* IE6-IE8 */ url('#{$ll-font-path}/maitree/maitree-v10-latin-200.woff2')
      format('woff2'),
    /* Super Modern Browsers */
      url('#{$ll-font-path}/maitree/maitree-v10-latin-200.woff') format('woff'),
    /* Modern Browsers */
      url('#{$ll-font-path}/maitree/maitree-v10-latin-200.ttf')
      format('truetype'),
    /* Safari, Android, iOS */
      url('#{$ll-font-path}/maitree/maitree-v10-latin-200.svg#Maitree')
      format('svg'); /* Legacy iOS */
}
